.container{
    display: grid;
    grid-template-columns: 5fr 1fr;
    grid-row: 1;
    grid-column: 2;
    height: 80%;
    width: 100%;
}
svg{
    fill: #296d98;
}
/* edit button */
.edit{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    border: none;
}
.edit-box{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: 15px;
    background-color: transparent;
    outline: none;
    border: none;
    margin-left: 5px;
    overflow: hidden;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;

}
.display{
    font-size: 15px;
    display: flex;
    align-items: center;
    overflow: hidden;
    font-family: 'Roboto', sans-serif;

}